@import url('https://fonts.googleapis.com/css2?family=Dosis:wght@800&family=Josefin+Sans:wght@100&family=Nova+Round&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');

$font-1: 'Nova Round', cursive;
$font-2: 'Dosis', sans-serif;
$font-3: 'Josefin Sans', sans-serif;
$font-4: 'Permanent Marker', cursive;
$font-5: 'Roboto', sans-serif;

$color-1: #12052D;
$color-2: #2A0A43;
$color-3: #24105A;
$color-4: #F43162;
$color-5: #251E8A ;
$color-11: #2a1d52;
$color-12: #6C77F4;
$color-13: rgba(99, 164, 233, 0.5);
$color-14: #4FEDD2;
$color-15: #36D0EA;
$white: #ddd;
$black: #333;

$borderRadius: 25px;
$shadow: 0 1px 3px #333;
$minHeight: 95vh;

@mixin verticalCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 1rem auto;
  max-width: 1200px;
  font-family: $font-1;
  color: black;
}

button {
  cursor: pointer;
}

li {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: pointer;
}

a:visited {
  color: $black;
}

.home, .knowledges, .portfolio, .contact {
  display: flex;

  @media screen and (max-width: 850px) {
    display: block !important;
  }
}

.homeContent, .knowledgesContent, .portfolioContent, .contactContent {
  margin-left: .4rem;

  @media screen and (max-width:850px){
    margin-left: 0 !important;
  }
}
